body {
  padding: 5em;
  background-color: #e3e3e3;
}

html {
}

*:before,
*:after {
  box-sizing: inherit;
  outline: none;
}

@font-face {
  font-family: 'Genericons';
  src: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/53819/genericons-regular-webfont.eot');
  src: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/53819/genericons-regular-webfont.woff') format('woff'),
  url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/53819/genericons-regular-webfont.eot') format('truetype');
  font-weight: normal;
  font-style: normal;
}

[class*="genericon"] {
  display: inline-block;
  width: 16px;
  height: 16px;
  -webkit-font-smoothing: antialiased;
  font-size: 16px;
  line-height: 1;
  font-family: 'Genericons';
  text-decoration: inherit;
  font-weight: normal;
  font-style: normal;
  vertical-align: top;
}

/* IE7 */
[class*="genericon"] {
  *overflow: auto;
  *zoom: 1;
  *display: inline;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  float: left;
  margin-right: 25px;
}

a .social-button {
  display: inline-block;
	box-sizing: border-box;
  width: 129px;
  height: 34px;
  padding-right: 12px;
  color: #fff;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.9;
  text-align: left;
  border-radius: 4px;
  background-color: #d0d0d0;
  
  /* transition fadeIn */
	transition: background-color 0.175s ease-in-out;
}

a .social-button.twitter {
  background-color: #00a9f1;
  width: 250px;
  /* font-size: 62.5%; */
}

